html, body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}

body {
    background: #000000;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

#game-container {
    position: relative;

    width: min(100vw, 100vh * (16 / 9));
    height: min(100vh, 100vw / (16 / 9));

    background: #222222;
    border-radius: 6px;
    overflow: hidden;

    display: flex;
    align-items: center;
    justify-content: center;
}

